import React from "react";

function Ingresar(props){
    const data = e => {
        //e.preventDefault()
        const detalle = document.getElementById("detalle").value
        const monto = document.getElementById("monto").value
        
        const obj = {
            detalle: detalle,
            monto: parseInt(monto)
        }
        console.log('submit')
        props.onSubmit(obj)

        document.getElementById('detalle').value = ""
        document.getElementById('monto').value = ""
    }

    return (
        <div id="ingresar" className="rounded-2xl p-5">
            
            <form className="flex h-full flex-col items-center" onSubmit={data}>
                <div>
                    <span className="bg-white rounded-s me-2 p-1">Egreso</span>
                    <span className="bg-white rounded-s p-1">Ingreso</span>
                </div>
                Detalle
                <input id='detalle' placeholder="detalle" className=" w-4/6"/>
                Monto
                <input id="monto" placeholder="monto" className=" w-4/6"/>
                <button className="bg-white rounded-2xl w-24 mt-2">Ingresar</button>
            </form>
        </div>
    )
}

export default Ingresar