
import './App.css';
import React,{useState} from 'react';
import Nav from './components/Nav';
import Estadistica from './components/Estadistica';
import Calendario from './components/Calendario';
import Ingresar from './components/Ingresar'
import Lista from './components/Lista';
import Footer from './components/Footer';
import Botonera from './components/Botonera';



function App() {
  if (!localStorage.getItem('db')){
    localStorage.setItem("db", JSON.stringify([{
      detalle:"papas",
      monto: 1200
    }]))
  }
  const [data, setData] = useState(JSON.parse(localStorage.getItem('db')))
  
  const actualizar = (obj) => {
    let nuevo = data;
    nuevo.info = [...data.info, obj]
    
    setData(nuevo)
    localStorage.setItem('db', JSON.stringify(data))
    console.log(data.info)
  }

  
  
  return (
    <div id='app'className='h-full gap-5 rounded-md bg-red-200 p-5 sm:bg-black'>
      <Nav/>
      <Estadistica />
      <Calendario />
      <Ingresar onSubmit={actualizar}/>
      <Lista data={data}/>
      <Botonera/>
      <Footer/>
    </div>
  )
  
}

export default App;
