import React from "react";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import { faXmark } from "@fortawesome/free-solid-svg-icons";


function Lista({data}){
    const eliminar = (key) => { data.info.slice(key); console.log('data')}
    return (
        <div id="lista" className="rounded-2xl p-2">
            {data.info.map((element,index)=>
                <p className="bg-slate-200 rounded-s w-4/6 flex justify-between items-center px-2" key={index}>{`${element.detalle} - ARS${element.monto}`}<FontAwesomeIcon icon={faXmark} onClick={eliminar(index)}/></p>
            )}
        </div>
    )
}

export default Lista