import React from "react";

function Botonera(){
    
    
    
    function cambiar(n){
        let calendario = document.getElementById('calendario')
        let estadistica = document.getElementById('estadistica')
        let ingresar = document.getElementById('ingresar')
        let lista = document.getElementById('lista')
        let array = [calendario, estadistica, ingresar,lista]
        
        array.forEach((element,index)=>{
            if(index === n){
                element.style.zIndex = "1"
            } else {
                element.style.zIndex = "0"
            }
        })
    }
    return (
        <div className="bg-pink-200 " id="btn-container">
            <div className="bg-slate-200 col p-6 btn" onClick={()=>cambiar(0)}>1</div>
            <div className="bg-slate-400 p-6 btn" onClick={()=>cambiar(1)}>2</div>
            <div className="bg-slate-600 p-6 btn" onClick={()=>cambiar(2)}>3</div>
            <div className="bg-slate-800 p-6 btn" onClick={()=>cambiar(3)}>4</div>
        </div>
    )
}

export default Botonera